<template>
    <div class="money_wrap">
        <section v-if="!in_">
        <Header :left_msg="left" :middle_msg="middle" :right_msg="right"></Header>
        <div class="one">
            <div class="two">
                <div class="three">
                    <span class="three_spn1"></span>
                    <span class="three_spn2" @click="in_=!in_">
                            <i class="el-icon-question"></i>
                            <span></span>
                    </span>
                    <br>
                    <p><span class="three_spn3">{{this.$store.state.res&&this.$store.state.res.data.balance||balance}}</span><span class="three_spn4"></span></p>
                    <button class="btn"></button>
                </div>
            </div>
        </div>
        <p class="jiaoyi">30</p>
        <div class="no_log">
            <img src="../../../assets/none.png" alt="">
            <p>30</p>
            <p style="font-size:10px;margin-top:10px;"></p>
        </div>
        </section>
        <section v-if="in_">
            <i class="el-icon-arrow-left" style="z-index:111;position:fixed;color:white;font-size:30px;top:10px;left:10px;" @click="in_=!in_"></i>
            <Header left_msg="left" middle_msg="" :right_msg="right"></Header>
            <div class="balance_con">
            <h3>Q1: </h3>
            <p><br><br>=101=10<br><br>2000
            </p>
            <h3>Q2: </h3>
            <p></p>
            <h3>Q3: </h3>
            <p>33</p>
            <h3>Q4: </h3>
            <p></p>
            </div>
        </section>
    </div>
</template>

<script>
    import Header from "@/components/common/Header"
    export default {
        data() {
            return {
                left: "el-icon-arrow-left",
                middle: "",
                right: false,
                balance: '0',
                in_:false
            }
        },
        components: {
            Header,
        },
        methods: {

        },
        created() {
        }
    }
</script>

<style scoped>
    .money_wrap {
        position: absolute;
        width: 100%;
        top: 0;
        bottom: 0;
    }
    .one {
        width: 100%;
        height: 196px;
        background: #3190E8;
        position: relative;
        top: 52px;
        overflow: hidden;
    }
    .two {
        margin: 8px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: #FFFF;
        border-radius: 5px;
        overflow: hidden;
    }
    .three {
        margin: 10px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        overflow: hidden;
    }
    .three_spn1 {
        color: #666;
        font-weight: 400;
        font-size: 15px;
    }
    .three_spn2 {
        position: absolute;
        right: 0;
        color: #3190E8;
    }
    .three_spn3 {
        line-height: 60px;
        font-size: 44px;
    }
    .three_spn4 {
        font-size: 17px;
    }
    .btn {
        width: 100%;
        height: 54px;
        position: absolute;
        bottom: 0;
        font-size: 20px;
        color: white;
        border: none;
        border-radius: 5px;
        outline: none;
        background: #fa7304;
        font-weight: 550;
    }
    .jiaoyi {
        position: absolute;
        top: 250px;
        line-height: 53px;
        font-size: 16px;
        color: #999;
        left: 15px;
    }
    .no_log {
        text-align: center;
        margin-top: 100px;
    }
    .no_log img {
        width: 215px;
    }
    .no_log p {
        font-size: 20px;
        color: #999;
    }
</style>



