            <p></p>
            <h3>Q2: </h3>
            <p>2016122500:00</p>
            <h3>Q3:</h3>
            <p>15050</p>
            <h3>Q4:</h3>
            <p></p>
            </div>
       </div>
</template>

<script>
    import Header from "@/components/common/Header"
    export default {
        data() {
            return {
                left: "el-icon-arrow-left",
                middle: "",
                right: false,
            }
        },
        components: {
            Header,
        }
    }
</script>

<style>
   .balance_wrap {
        position: absolute;
        width: 100%;
        top: 0;
        bottom: 0;
    }
    .balance_con {
        position: absolute;
        top: 52px;
        left: 15px;
        right: 15px;
    }
    .balance_con h3 {
        line-height: 56px;
        color: #333;
             
    }
    .balance_con p {
        color: #666;
        font-size: 18px;
        line-height: 26px;
    }
</style>



